.header-logo {
  padding-top: 50px;
  width: 12rem;
  position: relative;
}

.header-moto {
  @extend h1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); // white-space: nowrap;
  width: 80%;
}
